.filter-controls {
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid #dee2e6;
  display: flex;
  align-items: end;
}

.control-group {
  display: inline-block;
  margin-right: 20px;
}

.control-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #495057;
  font-size: 0.9em;
}

.filter-input,
.highlight-input,
.view-select,
.sort-select {
  padding: 8px 12px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  font-size: 0.9em;
  background-color: white;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.filter-input:focus,
.highlight-input:focus,
.view-select:focus,
.sort-select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.filter-input,
.highlight-input {
  width: 200px;
}

.view-select,
.sort-select {
  width: 180px;
}

.control-actions {
  display: inline-block;
  vertical-align: bottom;
  margin-left: 20px;
}

.clear-button {
  background-color: #6c757d;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em;
  transition: background-color 0.15s ease-in-out;
}

.clear-button:hover {
  background-color: #5a6268;
}

.clear-button:active {
  background-color: #545b62;
}

@media (max-width: 768px) {
  .control-group {
    display: block;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .filter-input,
  .highlight-input,
  .view-select,
  .sort-select {
    width: 100%;
    max-width: 300px;
  }

  .control-actions {
    display: block;
    margin-left: 0;
    margin-top: 10px;
  }
}
