.phrase-list {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #fafafa;
}

.list-title {
  margin: 0 0 10px 0;
  color: #333;
  font-size: 1.5em;
  font-weight: bold;
}

.phrase-count {
  color: #666;
  font-size: 0.9em;
  margin-bottom: 15px;
}

.phrases-container {
  min-height: 800px;
  height: 800px;
  overflow-y: auto;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
  resize: vertical;
}

.phrase-item {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  border-bottom: 1px solid #eee;
  transition: background-color 0.2s;
}

.phrase-item:hover {
  background-color: #f5f5f5;
}

.phrase-item:last-child {
  border-bottom: none;
}

.phrase-text {
  flex: 1;
  font-family: "Courier New", monospace;
  font-size: 0.85em;
  color: #333;
  line-height: 1.4;
  word-wrap: break-word;
}

.phrase-count-badge {
  background-color: #007bff;
  color: white;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.8em;
  font-weight: bold;
  margin-left: 10px;
}

.file-breakdown {
  display: flex;
  gap: 10px;
  margin-left: 10px;
  font-size: 0.8em;
}

.dani-count {
  color: #28a745;
  font-weight: bold;
}

.ved-count {
  color: #dc3545;
  font-weight: bold;
}

.highlight {
  background-color: #ffeb3b;
  padding: 1px 2px;
  border-radius: 2px;
  font-weight: bold;
}

/* Scrollbar styling */
.phrases-container::-webkit-scrollbar {
  width: 8px;
}

.phrases-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.phrases-container::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.phrases-container::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Pattern examples styling */
.pattern-examples {
  margin-top: 10px;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border-left: 3px solid #007bff;
}

.examples-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.example-column {
  min-height: 200px;
  max-height: 400px;
  overflow-y: auto;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  background-color: white;
  padding: 8px;
}

.example-label {
  font-size: 0.8em;
  font-weight: bold;
  color: #495057;
  margin-bottom: 8px;
  padding-bottom: 4px;
  border-bottom: 1px solid #dee2e6;
}

.example-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.example-text {
  font-size: 0.75em;
  color: #6c757d;
  font-family: "Courier New", monospace;
  padding: 3px 6px;
  background-color: #f8f9fa;
  border-radius: 2px;
  border-left: 2px solid #007bff;
  word-wrap: break-word;
  line-height: 1.3;
}

/* Scrollbar styling for example columns */
.example-column::-webkit-scrollbar {
  width: 6px;
}

.example-column::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.example-column::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.example-column::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Individual pattern info styling */
.individual-pattern-info {
  display: flex;
  gap: 8px;
  margin-top: 8px;
  align-items: center;
}

.pattern-badge {
  background-color: #6c757d;
  color: white;
  padding: 2px 6px;
  border-radius: 12px;
  font-size: 0.7em;
  font-weight: bold;
}

.author-badge {
  padding: 2px 6px;
  border-radius: 12px;
  font-size: 0.7em;
  font-weight: bold;
}

.author-badge.dani {
  background-color: #28a745;
  color: white;
}

.author-badge.ved {
  background-color: #dc3545;
  color: white;
}
