.stats-panel {
  background-color: #e9ecef;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid #ced4da;
}

.stats-panel h3 {
  margin: 0 0 15px 0;
  color: #495057;
  font-size: 1.2em;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-bottom: 15px;
}

.stat-item {
  background-color: white;
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #dee2e6;
  text-align: center;
}

.stat-label {
  font-size: 0.8em;
  color: #6c757d;
  margin-bottom: 5px;
  font-weight: 500;
}

.stat-value {
  font-size: 1.1em;
  font-weight: bold;
  color: #495057;
}

.stats-description {
  background-color: white;
  padding: 15px;
  border-radius: 6px;
  border: 1px solid #dee2e6;
}

.stats-description p {
  margin: 0;
  color: #6c757d;
  font-size: 0.9em;
  line-height: 1.4;
}

@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
}
